@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: poppins, 'sans-serif' ;
    overflow-x: hidden;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav{
    background-color: #012A4A;
    color: #fff;
    height: 5vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
nav h3{
    color: rgba(97, 165, 194, 1);
}
.intro{
    /*background: linear-gradient(#5390d9, #80ffdb);*/
    background-color: rgba(97, 165, 194, 0.7);
    color: #013A63;
}
.intro{
    width: 100vw;
    height: 95vh;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.hero-intro h2{
    font-size: 50px;
}
.hero-intro p{
    font-size: 20px;
}

#logo{
    max-height: 430px;
    max-width: 500px;
}

.socials{
    width: 15vw;
    height: 6vh;
    display: flex;
    justify-content: space-between;
}
.fab{
    color: #013A63;
}
.container{
    background-color: #ECF0F3;
    width: 100vw;
    height: 90vh;
}
#skill-stack {
    font-size: 28pt;
    font-weight: bold;
    margin: 25px 10px 0 25px;
}
.skill-set, .title{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.frontend,.skill-meter{
    margin: 25px;
    width: 40vw;
    height: 55vh;
    background: #ECF0F3;
    box-shadow: -18px -18px 30px rgba(255, 255, 255, 0.9), 18px 18px 30px #D1D9E6;
    border-radius: 21px;
}
.front-png{
    width: 70px;
    height: 70px;
    margin: 5px;
}

.frontend{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.frontend{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
}

.skill-meter{
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}

