.progress{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

progress{
    width: 60%;
    height: 1vh;
    border: none;
}

progress::-moz-progress-bar {
    background-color: rgba(97, 165, 194, 0.7);
}

footer{
    width: 100vw;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
footer a{
    text-decoration: none;
}
